export default {
	onLoad() {
		uni.request({
			url: 'https://douban-api.uieee.com/v2/movie/in_theaters',
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded', 
			},
			data: {
				name:"name1"
			},
			success: res => {
				this.movielist = res.data.subjects;
				console.log(res);
			},
			fail: Error => {
				alert("Error!")
			},
		});
		uni.request({
			url: 'https://douban-api.uieee.com/v2/movie/coming_soon',
			method: 'POST',
			header: {
				'content-type': 'application/x-www-form-urlencoded', 
			},
			data: {
				name:"name2"
			},
			success: res => {
				this.movielist2 = res.data.subjects;
				console.log(res);
				this.flag="true"
			},
			fail: Error => {
				alert("Error!")
			},
		});
		this.$store.state.demolist = this.movielist;
		this.$store.state.demolist2 = this.movielist2;
	},
	data() {
		return {
			movielist:"",
			movielist2:"",
			flag:"false",
		}
	},
	methods: {
		go(){
			setTimeout(() => {
				this.$store.state.demolist = this.movielist;
				this.$store.state.demolist2 = this.movielist2;
				uni.switchTab({
					url:"../index/index"
				})
			},1000)
		}
	}
}
